import { NgModule, CUSTOM_ELEMENTS_SCHEMA } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule } from '@angular/forms';
import { IonicModule } from '@ionic/angular';
import { RouterModule } from '@angular/router';
import { SharedModule } from './../../components/shared.module';

import { ItemDetailsActionSheetPage } from './item-details-action-sheet.page';

@NgModule({
  imports: [
    CommonModule,
    FormsModule,
    IonicModule,
    SharedModule,
    RouterModule.forChild([
      {
        path: '',
        component: ItemDetailsActionSheetPage
      }
    ])
  ],
  declarations: [ItemDetailsActionSheetPage],
  exports: [ItemDetailsActionSheetPage],
  schemas: [CUSTOM_ELEMENTS_SCHEMA]
})
export class ItemDetailsActionSheetPageModule { }
